.section {
    position: relative;
    overflow: hidden;
    padding-left: 8%;
    padding-right: 8%;
    padding-top: 70px;
    padding-bottom: 70px;
    border-bottom: 1px solid #585d65;
    z-index: 12;
    border-bottom-color: #e0e1e2;
}
.content {
    position: relative;
    max-width: 820px;
}
.titles {
    position: relative;
    margin: 0 0 60px 0;
}
.title {
    margin: 0 0 10px 0;
    max-width: 70%;
    font-size: 55px;
    line-height: 1.2;
    color: var(--title-color);
    font-weight: 700;
}
.subtitle {
    font-size: 14px;
    line-height: 1.2;
    color: #68e0cf;
    font-weight: 600;
    text-transform: uppercase;
}
.boxItems {
    position: relative;
    height: 812.62px;
}
.boxCol {
    padding: 40px 20px 0 20px;
    float: left;
    width: 50%;
}
.boxItem {
    position: relative;
    overflow: hidden;
    text-align: center;
}
.image {
    position: relative;
    line-height: normal;
}
.boxItem .image a {
    display: block;
    font-size: 0;
}
.boxItem .image a img {
    width: 100%;
    height: auto;
    position: relative;
    top: 0;
    max-width: 100%;
    border: none;
    border-radius: 0;
    box-shadow: none;
}
.info {
    text-align: center;
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    padding: 18px;
    transition: all 0.3s ease 0s;
}

.centralize {
    display: table !important;
    table-layout: fixed !important;
    height: 100% !important;
    position: relative !important;
    top: 0;
    left: 0;
    z-index: 2;
    background-color: #00bf9f;
    background: #68e0cf;
    max-width: 100%;
    width: 100%;
}
.boxItem:hover .info {
    opacity: 1;
}
.verticalCenter {
    display: table-cell !important;
    vertical-align: middle !important;
}
.icon {
    color: white;
    font-size: 21px;
    display: inline-block;
    transition: all 0.1s ease-in 0.4s;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
}
.desc {
    position: relative;
    padding: 15px 20px 0 20px;
}
.category {
    margin: 0;
    display: inline-block;
    font-size: 11px;
    color: #00bf9f;
    font-weight: 600;
    text-transform: uppercase;
}
.desc .name {
    height: 50px;
    display: block;
    font-size: 18px;
    color: #fff;
    font-weight: 800;
    transition: all 0.3s ease 0s;
    color: var(--paragraph-text-color);
    font-weight: 700;
    text-decoration: none;
}
#item2 {
    left: 332.3px;
    top: 0;
    position: absolute;
}
#item3 {
    left: 0;
    top: 266.925px;
    position: absolute;
}
#item4 {
    left: 332.3px;
    top: 266.925px;
    position: absolute;
}
#item5 {
    left: 0;
    top: 548.412px;
    position: absolute;
}

@media screen and (max-width: 1152px) {
    .content {
        max-width: 923px;
    }
    #item2 {
        left: 50%;
    }
    #item3 {
        top: 35%;
    }
    #item4 {
        top: 35%;
        left: 50%;
    }
    #item5 {
        top: 561.4px;
    }
}
@media screen and (max-width: 794px) {
    .title {
        max-width: 100%;
    }
    .titles {
        text-align: center;
    }
}
@media screen and (max-width: 692px) {
    .title {
        font-size: 41px;
    }
    .boxCol {
        width: 100%;
    }
    #item2 {
        left: 0;
        top: 370px;
    }
    #item3 {
        left: 0;
        top: 716px;
    }
    #item4 {
        left: 0;
        top: 1069px;
    }
    #item5 {
        left: 0;
        top: 1432px;
    }
    .boxItems {
        height: 1730px;
    }
}

@media screen and (max-width: 400px) {
    .titles {
        margin: 0 0 19px 0;
    }
    #item2 {
        top: 239px;
    }
    #item3 {
        top: 467px;
    }
    #item4 {
        top: 712px;
    }
    #item5 {
        top: 950px;
    }
    .boxItems {
        height: 1170px;
    }
}